#register-container {
  #register-form{
    font-size: 1em;
    box-shadow: $grad;
    .button{
      font-size: 1em;
    }
    .ui.checkbox{
      label{
        font-size: 1.5em;
      }
    }
  }
}

.forgot{
  text-align: center;
  margin-top: 1em;
  a{
    color: $purple;
  }
}

.hidden{
  display: none !important;
}

.prediction_input{
  border: 1px solid lightgray;
  padding-left: 1em;
  padding-right: 1em;
  padding-bottom: 0.36em;
  border-radius: 0.2em;
  width: 100%;
}
.add-prediction-form{
  .ui.divider{
    margin: 5px 0px 5px 0px;
  }
}
.add_divider{
  color: $black !important;
}
td{
  padding-left: 2px !important;
  padding-right: 4px !important;
}

@media only screen and (min-width : 1224px) {
  #register-form {
    width: 60%;
    margin: 0 auto;
  }
}



